@charset "UTF-8";

@import url(normalize.css);

@font-face {

}

/*------------------------------------------------------------------*/

/*Общие стили*/

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 300px;
  position: relative;
}

.wrap {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.ears::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

